
#latest-new {
    width: 100vw;
    height: 100vh;
    position: relative;
    /*top: 0;
    left: 0;*/
    overflow: hidden;
}
#latest-new>img {
    width: 100vw;
    height: 100vh;
    position: absolute;
    top: 0;
    left: 0;
    margin: 0;
    padding: 0;
}
#latest-new>.title {
    font-family: '小米兰亭';
    font-size: 5.556vh;
    line-height: 5.556vh;
    color: #f1f1f1;
    letter-spacing: 4px; 
    position: absolute;
    top: 6.481vh;
    left: 6.25vw;
}
#latest-new>.roll-pics-container {
    width: 87.5vw;
    height: 48vh;
    position: absolute;
    top: 14.815vh;
    left: 6.25vw;
    /* border: 1px solid red; */
    overflow: hidden;
}
#latest-new>.roll-pics-container>div {
    width: 900vw;
    height: 44.444vh;
    position: absolute;
    top: 0;
    /* left: -68.75vw; */
    /* border: 2px solid yellow; */
    /* transition: all 1s ease; */
}
#latest-new>.roll-pics-container>div .roll-pic {
    width: 75vw;
    height: 44.444vh;
    position: absolute;
    top: 0;
    -webkit-transform-origin: 0 100%;
            transform-origin: 0 100%;
}
#latest-new>ul {
    position: absolute;
    top: 62.037vh;
    left: 38.021vw;
    text-decoration: none;
    width: 20vw;
    height: 1vh;
}
#latest-new>ul>li {
    position: absolute;
    top: 0;
    left: 0;
    height: 1vh;
    width: 20vw;
}
#latest-new>ul>li>span {
    width: 2.083vw;
    height: 0.278vh;
    position: absolute;
    top: 0;
}
#latest-new>.spots-title {
    font-family: '小米兰亭';
    font-size: 2.407vh;
    line-height: 2.407vh;
    color: #f1f1f1;
    letter-spacing: 2px; 
    position: absolute;
    top: 64.815vh;
    left: 6.25vw;
}
#latest-new>.small-pics-container {
    width: 357.031vw;
    height: 27.778vh;
    position: absolute;
    top: 69.444vh;
    -webkit-transition: all 1s ease;
    transition: all 1s ease;
}
#latest-new>.small-pics-container .small-pics {
    width: 15.625vw;
    height: 27.778vh;
    position: absolute;
    top: 0;
    -webkit-transition: all 0.6s ease;
    transition: all 0.6s ease;
}



