
#favorite-new {
    width: 100vw;
    height: 100vh;
    position: absolute;
    top: 0;
    left: 0;
    overflow: hidden;
}
#favorite-new>img{
    width: 100vw;
    height: 100vh;
    position: absolute;
    top: 0;
    left: 0;
}
#favorite-new>.title {
    font-family: "小米兰亭";
    font-size: 5.556vh;
    color: #f1f1f1;
    letter-spacing: 2px;
    position: absolute;
    top: 6.481vh;
    left: 6.25vw;
}
#favorite-new>.photo-amount {
    font-family: "小米兰亭";
    font-size: 2.222vh;
    color: #f1f1f1;
    letter-spacing: 0.093vh;
    position: absolute;
    top: 14vh;
    left: 6.25vw;
}
#favorite-new>.first-pic {
    width: 33.33vw;
    height: 59.26vh;
    position: absolute;
    top: 20.37vh;
    left: 6.25vw;
    -webkit-transition: all 0.5s ease;
    transition: all 0.5s ease;
}
#slide-img-container {
    width: 68.75vw;
    height: 100vh;
    /* border: 1px solid red;        */
    position: absolute;
    top: 0vh;
    left: 39.586vw;
    overflow: hidden;
}
#slide-img-container>div {
    width: 68.75vw;
    height: 59.26vh;
    position: absolute;
    /* border: 1px solid yellow;       */
    top: 20.37vh;
    -webkit-transition: all 1s ease;
    transition: all 1s ease;
}
#slide-img-container>div .small-pic {
    width: 15.625vw;
    height: 27.78vh;
    position: absolute;
    /* margin-right: 2.083vw;
    margin-bottom: 3.704vh; */
    -webkit-transition: all 0.5s ease;
    transition: all 0.5s ease;
}



