
@font-face {
  font-family: font757;
  src: url("../assets/font/小米兰亭.ttf");
}
#picture-dialog span,#picture-dialog div{
	font-family: font757;
	letter-spacing: 0.1vw;
	color: white;
/*	padding: 0;
	margin:0;*/
}
#picture-dialog{
	background-position: center;
	background-repeat: no-repeat;
	background-size: cover;
	position: relative;
	display: inline-block;
	-webkit-transition: all 0.4s ease;
	transition: all 0.4s ease;
	text-align:center;
}

/*#picture-dialog :before{
	content: ".";
	height: 100%;
	display: inline-block;
	vertical-align: middle;
	visibility: hidden;
}*/
.middleTitle{
	position: relative;
	font-size: 2vw;
	top:40%;
	width: 100%;
	background: -webkit-linear-gradient(0,rgba(49,71,127,0) 20%,rgba(49,71,127,0.5),rgba(49,71,127,0) 80%);
}
#picture-dialog > .text_bg{
	width:100%;
	/*min-height: 6.8vh;*/
	/*max-height: 11vh;*/
	height: 15%;
	bottom: 0;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-pack: center;
	    -ms-flex-pack: center;
	        justify-content: center;
	-webkit-box-orient: vertical;
	-webkit-box-direction: normal;
	    -ms-flex-direction: column;
	        flex-direction: column;
	position: absolute;
	background: rgba(49,71,127,0.5);
	/*display: table;*/
	-webkit-transition: all 0.4s ease;
	transition: all 0.4s ease;
}
#picture-dialog > .text_bg > .title{
	position: relative;
	top:0;
	width: 100%;
	text-align: center;
	display: table-cell;
	vertical-align: top;
	font-size: 3vw;
	color: white;
	-webkit-transition: all 0.4s ease;
	transition: all 0.4s ease;
}
#picture-dialog > .text_bg > .info_box{
	font-size: 1.3vw;
	position: absolute;
	bottom: 10%;
	left:2%;
	-webkit-transition: all 0.4s ease;
	transition: all 0.4s ease;
}
#picture-dialog > .text_bg > .ico_box{
	position: absolute;
	bottom: 10%;
	right: 2%;
	text-align: right;
}
#picture-dialog > .text_bg > .ico_box > .like{
	position:relative;
	font-size: 1.3vw;
	-webkit-transition: all 0.4s ease;
	transition: all 0.4s ease;
}
#picture-dialog > .text_bg > .ico_box > .visited{
	position:relative;
	font-size: 1.3vw;
	margin-right: 2.6vw;
	-webkit-transition: all 0.4s ease;
	transition: all 0.4s ease;
}
#picture-dialog > .text_bg > .ico_box >.like > img{
	/*height: 150%;*/
	position: absolute;
	bottom: 0;
	right:110%;
	height:120%;
}
#picture-dialog > .text_bg > .ico_box > .visited > img{
	/*height: 150%;*/
	position: absolute;
	bottom: 0;
	right:110%;
	height:120%;
}
.fade-enter-active, .fade-leave-active {
  -webkit-transition: all 0.6s;
  transition: all 0.6s
}
.fade-enter, .fade-leave-to /* .fade-leave-active in below version 2.1.8 */ {
  opacity: 0
}



