
#upload-photo {
    width: 100vw;
    height: 100vh;
    position: absolute;
    top: 0;
    left: 0;
    overflow: hidden;
}
#upload-img {
    width: 100vw;
    height: 100vh;
    position: absolute;
    top: 0;
    left: 0;
}
#upload-photo>.upload-title{
    font-family: "小米兰亭";
    font-size: 5.556vh;
    color: #f1f1f1;
    letter-spacing: 2px;
    position: absolute;
    top: 6.481vh;
    left: 6.25vw;
}
#upload-photo>.qr-upload-container {
    width: 21.875vw;
    height: 46.296vh;
    position: absolute;
    top: 23.148vh;
    left: 39.0625vw;
}
#upload-photo>.qr-upload-container p {
    font-family: "小米兰亭";
    font-size: 3.333vh;
    text-align: center;
    margin: 0 auto;
    color: #f1f1f1;
}
#qr-upload {
    width: 21.875vw;
    height: 38.889vh;
    position: absolute;
    left: 0;
    bottom: 0;
} 
