
.wrapper {
  background: url("../assets/blue.png") no-repeat;
  width: 100vw;
  height: 100vh;
  overflow: hidden;
}
.wrapper #outer-box {
    /*padding-right: 300px;*/
    overflow: hidden;
    width: 100vw;
    height: 100vh;
}
.wrapper #outer-box #container {
      /*position: absolute;*/
      z-index: 0;
      width: 120vw;
      height: 120vh;
}
.wrapper #outer-box #panel {
      border: 1px solid red;
      background-color: #3F4458;
      position: absolute;
      top: 0;
      bottom: 0;
      left: 0;
      overflow: hidden;
      z-index: 999;
      color: #ddd;
      /*width: 10vw;*/
}
.wrapper #outer-box #panel #panel::-webkit-scrollbar {
        display: none;
}
.wrapper #outer-box #panel #area-tree {
        border: 1px solid black;
}
.wrapper #outer-box #panel #area-tree h2 {
          display: inline-block;
          /*font-weight: 500;*/
          font-size: 1.5vw;
          padding: 0.3vw 0.55vw;
          margin: 0;
}
.wrapper #outer-box #panel #area-tree h2.selected {
            background: #ddd;
            color: #fff;
}
.wrapper #outer-box #panel #area-tree ul {
          margin-left: 2vw;
          clear: both;
}
.wrapper #outer-box #panel #area-tree li {
          margin: 0.5vw 0 0 0;
          font-size: 1.2vw;
}
.wrapper #outer-box #panel #area-tree li.lv_province {
            margin: 0.5vw 0.5vw 0.5vw 0;
}
.wrapper #outer-box #panel #area-tree ul.sublist.lv_country {
          display: -webkit-box;
          display: -ms-flexbox;
          display: flex;
          -webkit-box-orient: vertical;
          -webkit-box-direction: normal;
              -ms-flex-direction: column;
                  flex-direction: column;
}
.hide-sub > ul {
  display: none;
}



