
@font-face {
    font-family: font757;
    src: url("../assets/font/小米兰亭.ttf");
}
body {
    width: 100vw;
    height: 100vh;
    overflow: hidden;
}
#index{
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    overflow: hidden;
    width: 200vw;
    position: relative;
    background: #fff;
}
.item_carousel{
    display: inline-block;
    position: fixed;
    width: 20vw;
    height: 100vh;
    -webkit-transform: skew(-7.5deg);
            transform: skew(-7.5deg);
    overflow: hidden;
    -webkit-transition-property: left,width;
    transition-property:left,width;
    -webkit-transition-duration: 0.6s;
            transition-duration: 0.6s;
}
.item_carousel img{
    position: relative;
    left: -20vw;
    top: 0px;
    -webkit-transform: skew(7.5deg);
            transform: skew(7.5deg);
    height: 100vh; 
    -webkit-transition: all 0.6s ease; 
    transition: all 0.6s ease;
}
.item_carousel span{
    font-family: font757;
    position: absolute;
    bottom:5.55vh;
    text-align: center;
    width: 100%;
    left:0;
    letter-spacing: 0.5vw;
    -webkit-transform: skew(7.5deg);
            transform: skew(7.5deg);
    -webkit-transition: all 0.6s ease;
    transition: all 0.6s ease;
    color: #f1f1f1;
    font-size: 2.3vw;
}

  /*
