
#panorama {
    width: 100vw;
    height: 100vh;
    position: absolute;
    top: 0;
    left: 0;
    background: green;
    overflow: hidden;
}
#panorama .panorama-pic {
    width: 100vw;
    height: 100vh;
    position: absolute;
    top: 0;
    left: 0;
    overflow: hidden;
}
#panorama>.pic-info {
    width: 100vw;
    height: 20vh;
    position: absolute;
    top: 0;
    left: 0;
    /* border: 2px solid yellow; */
}
#panorama>.pic-info>div>.icon {
    width: 1.927vw;
    height: 3.426vh;
    position: absolute;
    top: 1.481vh;
    left: 0.833vw;
}
#panorama>.pic-info>div>.title {
    position: absolute;
    top: 1.852vh;
    left: 2.76vw;
    font-family: "小米兰亭";
    font-size: 2.222vh;
    color: #f1f1f1;
    text-shadow: 0 0 0.278vh #000000;
}
#panorama>.pic-info>div>.visited-icon {
    width: 1.458vw;
    height: 1.667vh;
    position: absolute;
    top: 5.185vh;
    left: 3.646vw;
}
#panorama>.pic-info>div>.visited-amount {
    position: absolute;
    top: 5.185vh;
    left: 5.365vw;
    font-family: "小米兰亭";
    font-size: 1.852vh;
    color: #f1f1f1;
    text-shadow: 0 0 0.278vh #000000;
}
#panorama>.pic-info>div>.like-icon {
    width: 1.042vw;
    height: 1.667vh;
    position: absolute;
    top: 5.185vh;
    left: 8.333vw;
}
#panorama>.pic-info>div>.like-amount {
    position: absolute;
    top: 5.185vh;
    left: 9.635vw;
    font-family: "小米兰亭";
    font-size: 1.852vh;
    color: #f1f1f1;
    text-shadow: 0 0 0.278vh #000000;
}
#panorama>.navigation {
    width: 60vw;
    height: 12.037vh;
    position: absolute;
    top: 77.315vh;
    left: 24.74vw;
}
#panorama>.navigation>ul {
    position: absolute;
    top: 0;
    left: 0;
    text-decoration: none;
}
#panorama>.navigation li {
    position: relative;
    float: left;
}
#panorama>.navigation li>div {
    width: 6.771vw;
    height: 12.037vh;
    margin-right: 1.979vw;
    margin-top: 0;
    padding: 0;
}
#panorama .no-focus {
    background-color: #7d7070;
    opacity: 0.5;
}
#panorama .focus {
    background-color: #dddddd;
    opacity: 0.7;
}
#panorama .like>img {
    width: 4.167vw;
    height: 6.481vh;
    position: absolute;
    top: 2.778vh;
    left: 1.302vw;
}
#panorama .continute>p {
    width: 4.167vw;
    height: 7.407vh;
    position: absolute;
    top: 2.265vh;
    left: 1.462vw;
    margin: 0;
    padding: 0; 
    font-family: "小米兰亭";
    color: rgba(0, 0, 0, 0.65);
    font-size: 3.241vh;
    letter-spacing: 0.20vw;
    line-height: -0.926vh;
    text-align: center;
}
#panorama .prev-photo>div{
    width: 4.167vw;
    height: 9.722vh;
    position: absolute;
    top: 2.315vh;
    left: 1.302vw;
}
#panorama .next-photo>div{
    width: 4.167vw;
    height: 9.722vh;
    position: absolute;
    top: 2.315vh;
    left: 1.302vw;
}
#panorama .comment>div{
    width: 4.167vw;
    height: 9.722vh;
    position: absolute;
    top: 2.315vh;
    left: 1.302vw;
}
#panorama .detail>div{
    width: 4.167vw;
    height: 9.722vh;
    position: absolute;
    top: 2.315vh;
    left: 1.302vw;
}
#panorama .prev-photo>div>img{
    width: 3.281vw;
    height: 4.537vh;
    position: absolute;
    top: 0;
    left: 0.468vw;
}
#panorama .next-photo>div>img{
    width: 3.281vw;
    height: 4.537vh;
    position: absolute;
    top: 0;
    left: 0.468vw;
}
#panorama .comment>div>img{
    width: 3.021vw;
    height: 4.907vh;
    position: absolute;
    top: 0;
    left: 0.573vw;
}
#panorama .detail>div>img{
    width: 2.917vw;
    height: 1.111vh;
    position: absolute;
    top: 1.296vh;
    left: 0.625vw;
}
#panorama li>div>div>p {
    width: 4.167vw;
    height: 2.87vh;
    position: absolute;
    top: 5.2vh;
    left: 0.3vw;
    font-size: 2.037vh;
    letter-spacing: 0.0556vh;
}
.show-photo-enter-active,.show-photo-leave-active {
    -webkit-transition: all 0.7s ease;
    transition: all 0.7s ease;
}
.show-photo-enter {
    opacity: 0.3; 
    -webkit-transform: skew(75deg,75deg); 
            transform: skew(75deg,75deg);
}
.show-photo-leave-to {
    opacity: 0.3; 
    -webkit-transform: skew(75deg,75deg); 
            transform: skew(75deg,75deg);
}
.show-comments-enter-active {
    -webkit-transition: all 0.7s ease;
    transition: all 0.7s ease;
}
.show-comments-leave-active {
    -webkit-transition: all 0.7s cubic-bezier(1.0, 0.5, 0.8, 1.0);
    transition: all 0.7s cubic-bezier(1.0, 0.5, 0.8, 1.0);
}
.show-comments-enter{
    -webkit-transform: translateX(20vw);
            transform: translateX(20vw);
    opacity: 0;
}
.show-comments-leave-to {
    -webkit-transform: translateX(20vw);
            transform: translateX(20vw);
    opacity: 0;
}
.show-details-enter-active,.show-details-leave-active {
    -webkit-transition: all 0.5s ease;
    transition: all 0.5s ease;
}
.show-details-enter{
    -webkit-transform: skewX(90deg);
            transform: skewX(90deg);
}
.show-details-leave-to {
    -webkit-transform: skewX(90deg);
            transform: skewX(90deg);
}



