
#works {
  width: 100vw;
  height: 100vh;
  margin: 0;
  padding: 0;
  overflow: hidden;
}
#works>img {
  width: 100vw;
  height: 100vh;
  position: absolute;
  top: 0;
  left: 0;
  margin: 0;
  padding: 0;
}
#works>.title {
  font-family: "小米兰亭";
  font-size: 5.556vh;
  color: #f1f1f1;
  letter-spacing: 0.185vh;
  position: absolute;
  top: 6.481vh;
  left: 6.25vw;
}
#works>.photo-amount {
  font-family: "小米兰亭";
  font-size: 2.222vh;
  color: #f1f1f1;
  letter-spacing: 0.093vh;
  position: absolute;
  top: 14vh;
  left: 6.25vw;
}
#upload {
  width: 6.51vw;
  height: 4.907vh;
  margin: 0;
  padding: 0;
  position: absolute;
  z-index: 15;
  top: 7.5vh;
  left: 20.677vw;
}
#upload>img {
  width: 2.76vw;
  height: 4.907vh;
  position: absolute;
  top: 0;
  left: 0;
}
#upload>span {
  font-family: "小米兰亭";
  font-size: 1.852vh;
  color: #f1f1f1;
  width: 3.75vw;
  height: 4.907vh;
  text-align: center;
  margin: 0 auto;
  padding-top: 13px;
  letter-spacing: 1px;
  position: absolute;
  top: 0;
  left: 2.76vw;
}
#works-container {
  width: 100vw;
  height: 100vh;
  /* border: 1px solid red;  */
  position: absolute;
  top: 0;
  left: 0;
  overflow: hidden;
  -webkit-transition: all 1s ease;
  transition: all 1s ease;
}
.confirm-enter-active, .confirm-leave-active {
    -webkit-transition: all 0.7s cubic-bezier(0.075, 0.82, 0.165, 1);
    transition: all 0.7s cubic-bezier(0.075, 0.82, 0.165, 1);
}
.confirm-enter, .confirm-leave-to {
    -webkit-transform: translateY(-55vh);
            transform: translateY(-55vh);
    opacity: 0;
}
#delete-explain {
    font-family: "小米兰亭";
    color: #f1f1f1;
    font-size: 2.963vh;
    position: absolute;
    right: 6.25vw;
    bottom: 6.481vh;
}



