
@font-face {
  font-family: font757;
  src: url("../assets/font/小米兰亭.ttf");
}
#spots div{
  font-family: font757;
  color: white;
  font-size: 2.96vh;
  /*letter-spacing: 1vh;*/
}
#spots{
  background-size: cover;
  height: 100vh;
  width: 100vw;
  position: relative;
  overflow: hidden;
}
#city{
  position: absolute;
  top:6.48vh;
  left:6.25vw;
}
#date{
  position: absolute;
  top:6.48vh;
  right:6.25vw;
}
#button_re{
  position: absolute;
  height:100vh;
  width:5vw;
  left:0;
  /* background:red; */
}
#button_ad{
  position: absolute;
  height:100vh;
  width:5vw;
  right:0;
  /*background:green;*/
}
#spotsList{
  position: absolute;
  top:18.33vh;
  left:5.2vw; 
  width: 100vw;
  height: 59.9vh;
}
#spotsList li{
  display: inline-block; 
   position: relative;
}
#spotsList > .noSelect{
  -webkit-box-shadow: 0 0 0 rgba(0,0,0,0.7);
          box-shadow: 0 0 0 rgba(0,0,0,0.7);
  width:18.75vw;
  height: 53.89vh;
  margin-right:4vw;
  vertical-align: top; 
  top:3vh;
  -webkit-transition: all 0.4s;
  transition: all 0.4s
}
#spotsList > .noSelect > .pic{
  width:18.75vw;
  height: 53.89vh;
}
#spotsList > .select{
  -webkit-box-shadow: 0 0 5vw rgba(0,0,0,0.7);
          box-shadow: 0 0 5vw rgba(0,0,0,0.7);
  width:20.83vw;
  height: 59.9vh;
  margin-right: 3.125vw;
  top:0;
  vertical-align: top; 
  -webkit-transition: all 0.4s; 
  transition: all 0.4s
}
#spotsList > .select > .pic{
  width:20.83vw;
  height: 59.9vh;
}
#pageIndex{
  width: 100%;
  text-align: center;
  position: absolute;
  bottom:8.52vh;
}
#pageIndex > li{
  display: inline-block;
  vertical-align: middle;
  height:100%;
  margin-left:1.3vw;
  margin-right: 1.3vw;
}
#pageIndex > li >img{
  display: inline-block;
  height:3vh;
}
.indexFade-enter-active, .indexFade-leave-active {
  -webkit-transition: all 0.2s;
  transition: all 0.2s
}
.indexFade-enter, .indexFade-leave-to  {
  opacity: 0.5
}
.fade-enter-active, .fade-leave-active {
  -webkit-transition: all 0.4s;
  transition: all 0.4s
}
.fade-enter, .fade-leave-to {
  opacity: 0
}     

/*#pageIndex > li > img{
  position: relative;
}*/



