
.sub-work {
    width: 20.833vw;
    height: 54.833vh;
    position: absolute;
    top: 26.648vh;
}
.sub-work>.time-container {
    width: 100%;
    position: absolute;
    height: 4vh;
    z-index: 5;
}
.sub-work>.time-container>p {
    font-family: '小米兰亭';
    color: #f1f1f1;
    font-size: 2.222vh;
    text-align: center;
    padding: 0 auto;
    z-index: 5;
}
.sub-work>.camera-container {
    width: 100%;
    height: 6.389vh;
    position: absolute;
    z-index: 4;
    margin-top: 4vh;
}
.sub-work>.camera-container>div {
    margin-left: 8.594vw;
    width: 3.597vw;
    height: 100%;
    position: absolute;
    z-index: 4;
}
.sub-work>.camera-container>div>img {
    width: 100%;
    height: 100%;
    position: absolute;
    z-index: 4;
}
.sub-work>.work-img {
    width: 100%;
    height: 44.444vh;
    position: absolute;
    z-index: 2;
    margin-top: 10.389vh;
    -webkit-transition: all 0.7s ease;
    transition: all 0.7s ease;
    /* border: 1px solid red; */
    /* background: url(../assets/favorite/bg1.jpg) no-repeat center center; */
}
.sub-work>.work-img>img {
    width: 100%;
    height: 100%;
    position: absolute;
    z-index: 1;
}
.sub-work>.work-img>div {
    width: 100%;
    height: 7.407vh;
    position: absolute;
    z-index: 2;
    bottom: 0;
    font-family: '小米兰亭';
    color: #f1f1f1;
    letter-spacing: 0.4vh;
    background-color: #31477f;
    opacity: 1;
    -webkit-transition: all 0.7s ease;
    transition: all 0.7s ease
}
.sub-work>.work-img>div>p {
    position: absolute;
    width: 100%;
    z-index: 2;
    text-align: center;
    margin: 1.852vh auto;
}
.sub-work>.line {
    width: 27.083vw;
    height: 0.185vh;
    position: absolute;
    left: 10.417vw;
    top: 7.195vh;
    z-index: 1;
    border: none;
    border-top: 0.093vh solid #f1f1f1;
}



