
@font-face {
  font-family: font757;
  src: url("../assets/font/小米兰亭.ttf");
}
#College{
    position: relative;
    height: 100vh;
    width: 100vw;
    overflow: hidden; 
    font-family: font757;
    background: url('../assets/collage/bg0.png');
    color: #f1f1f1;
    background-size: cover;
}
#searchBox{
    position: absolute;
    width: 10.42vw;
    height: 100vh;
    overflow: hidden;
    overflow-y: scroll;  
    left: 0;
}
#searchBox::-webkit-scrollbar {
    display: none;
}
#searchBox::-moz-scrollbar{
    display: none;
}
#searchBox::-ms-scrollbar{
    display: none;
}
#collegeDiv{
    position: relative;
    width: 89.58vw;
    left: 10.42vw;
}
#date{
  position: absolute;
  font-size: 1.2vw;
  top:6.48vh;
  right:10vw;
}
#collegeList{
  position: absolute;
  top:18.33vh;
  left:2.30vw; 
  width: 89.58vw;
  height: 59.9vh;
}
#collegeList li{
  display: inline-block; 
   position: relative;
}
#collegeList > .noSelect{
  -webkit-box-shadow: 0 0 0 rgba(0,0,0,0.7);
          box-shadow: 0 0 0 rgba(0,0,0,0.7);
  width:18.75vw;
  height: 53.89vh;
  margin-right:3.02vw;
  vertical-align: top; 
  top:2.96vh;
  -webkit-transition: all 0.4s;
  transition: all 0.4s
}
#collegeList > .noSelect > .pic{
  width:18.75vw;
  height: 53.89vh;
}
#collegeList > .select{
  -webkit-box-shadow: 0 0 5vw rgba(0,0,0,0.7);
          box-shadow: 0 0 5vw rgba(0,0,0,0.7);
  width:20.83vw;
  height: 59.9vh;
  margin-right: 2.76vw;
  top:0;
  vertical-align: top; 
  -webkit-transition: all 0.4s; 
  transition: all 0.4s
}
#collegeList > .select > .pic{
  width:20.83vw;
  height: 59.9vh;
}
#pageIndex{
  width: 100%;
  text-align: center;
  position: absolute;
  /* bottom:8.52vh;   */
   top:90vh;
}
#pageIndex > li{
  display: inline;
  vertical-align: middle;
  margin-left:1.3vw;
  margin-right: 1.3vw;
}
.indexFade-enter-active, .indexFade-leave-active {
  -webkit-transition: all 0.2s;
  transition: all 0.2s
}
.indexFade-enter, .indexFade-leave-to  {
  opacity: 0.5
}
.fade-enter-active, .fade-leave-active {
  -webkit-transition: all 0.4s;
  transition: all 0.4s
}
.fade-enter, .fade-leave-to {
  opacity: 0
}     





