
body{
    src: url("../assets/font/小米兰亭.ttf");
    font-family: "小米兰亭";
}
#box{
     position: relative;
     width: 100vw;
     height: 100vh; 
     overflow: hidden;
     overflow-y: scroll; 
     left: 0;
}
#box::-webkit-scrollbar {
    display: none;
}
#searchBox::-moz-scrollbar{
    display: none;
}
#searchBox::-ms-scrollbar{
    display: none;
}
#searchContent{
   position: relative;
   width: 100%;
   height: 8.7vh;
}
#searchPic{
   position: absolute;
   width: 27.5%;
   height: 5.09vh;
   left:37%;
   top:1.85vh;
}
  /*暂定  */
#searchText{
    position: absolute; 
    /* width: 70%; */
    width: 100%;
    height: 8.4vh;
    top:0.2vh;
    opacity: 0;
}

  /* #search{
    position: absolute; 
    right: 0;
    width: 29%;
    height: 6vh;
  } */
#contentList{
    position: relative;  
    /* margin-top:9.26vh; */
    font-size: 1.56vw; 
    text-align: left;
    cursor: pointer;
}
#contentList li{
     position: relative;
     list-style-type: none;
     line-height: 8.7vh; 
     padding-left: 25%;
     /* padding-top:3.05vh; */
    /* padding-bottom: 2.96vh;  */
     color: #f1f1f1;
}
.picDivision{
    position: absolute;
    width: 16%;
    height: 0.167vh;
    top: 0;
    left: 0;
    opacity: 0.3;
}
.initClass{
    position: absolute;
    font-size: 0.977vw;
    color: #a1d3ff;
    top:-2vh;
    left:8.5%;
}

  /*高亮样式暂定  */
.hightLight {
    background: #5151A2  ;
    font-weight: bolder;
}
/*暂定  */
.searchHeightLight{
    background: #104E8B;
}




