
#User {
    width: 100vw;
    height: 100vh;
    position: absolute;
    top: 0;
    left: 0;
    /* background: green; */
}
#User>img {
    width: 100vw;
    height: 100vh;
    position: absolute;
    top: 0;
    left: 0;
}
#User>.title {
    font-family: '小米兰亭';
    font-size: 5.556vh;
    color: #f1f1f1;
    letter-spacing: 4px;
    position: absolute;
    top: 6.481vh;
    left: 6.25vw;
}
#user-logout {
    width: 31.25vw;
    height: 55.556vh;
    margin: 0;
    position: relative;
    top: 22.222vh;
    left: 6.25vw;
}
#user-logout>img {
    margin: 0;
    width: 12.5vw;
    height: 55.556vh;
    position: absolute;
    top: 0;
    left: 0;
}
#user-logout>div {
    width: 18.75vw;
    height: 55.556vh;
    position: absolute;
    margin: 0;
    top: 0;
    left: 12.5vw;
}
#user-logout>div>img {
    width: 18.75vw;
    height: 55.556vh;
    position: absolute;
    top: 0;
    left: 0;
}
#username-container {
    width: 18.75vw;
    height: 36.889vh;
    position: absolute;
    top: 0;
    left: 0;
}
#username-container>p {
    text-align: center;
    margin: 15vh auto 18vh;
    font-family: "小米兰亭";
    font-size: 3.704vh;
    color: #f1f1f1;
    letter-spacing: 2px;
}
#logout-container {
    width: 18.75vw;
    height: 18.667vh;
    position: absolute;
    top: 36.481vh;
    left: 0;
}
#logout-container>p{
    width: 13.542vw;
    height: 9.259vh;
    border: none;
    background: none;
    font-family: "小米兰亭";
    font-size: 4.444vh;
    color: #f1f1f1;
    text-align: center;
    margin: 0 auto;
    padding-top: 2vh;
    letter-spacing: 4px;
}
#User>.menu-container {
    width: 15.625vw;
    height: 55.556vh;
    position: absolute;
    top: 22.222vh; 
    -webkit-transition: all 0.7s ease; 
    transition: all 0.7s ease;
}
#User>.menu-container>img {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
}
#User>.menu-container>.icon-container {
    width: 100%;
    height: 70%; 
    position: absolute;
    top: 0;
    left: 0;
}
#User>.menu-container>.icon-container>div{
    width: 40%;
    text-align: center;
    margin: 12vh auto;
}
#User>.menu-container>.icon-container>div>img {
    width: 100%;
}
#User>.menu-container>.item-title-container {
    width: 100%;
    height: 30%;
    position: absolute;
    top: 70%;
    left: 0;
}
#User>.menu-container>.item-title-container>p {
    border: none;
    background: none;
    font-family: "小米兰亭";
    font-size: 4.444vh;
    color: #f1f1f1;
    text-align: center;
    margin: 0 auto;
    letter-spacing: 4px;
}
.confirm-enter-active, .confirm-leave-active {
    -webkit-transition: all 0.7s cubic-bezier(0.075, 0.82, 0.165, 1);
    transition: all 0.7s cubic-bezier(0.075, 0.82, 0.165, 1);
}
.confirm-enter, .confirm-leave-to {
    -webkit-transform: translateY(-55vh);
            transform: translateY(-55vh);
    opacity: 0;
}
.about-enter-active, .about-leave-active {
    -webkit-transition: all 0.7s cubic-bezier(0.075, 0.82, 0.165, 1);
    transition: all 0.7s cubic-bezier(0.075, 0.82, 0.165, 1);
}
.about-enter, .about-leave-to {
    -webkit-transform: translateX(-60vw);
            transform: translateX(-60vw);
    opacity: 0
}
#login-website {
    font-family: "小米兰亭";
    color: #f1f1f1;
    font-size: 2.963vh;
    position: absolute;
    right: 6.25vw;
    bottom: 6.481vh;
}



