
#tvlogin {
    width: 100vw;
    height: 100vh;
    position: absolute;
    top: 0;
    left: 0;
    overflow: hidden;
}
#tvlogin>img {
    width: 100vw;
    height: 100vh;
    position: absolute;
    top: 0;
    left: 0;
}
#tvlogin>.login-title{
    font-family: "小米兰亭";
    font-size: 5.556vh;
    color: #f1f1f1;
    letter-spacing: 2px;
    position: absolute;
    top: 6.481vh;
    left: 6.25vw;
}
#tvlogin>.qr-container {
    width: 21.875vw;
    height: 53.703vh;
    position: absolute;
    top: 23.148vh;
    left: 39.0625vw;
}
#tvlogin>.qr-container>p {
    font-family: "小米兰亭";
    font-size: 3.333vh;
    text-align: center;
    margin: 0 auto;
    color: #f1f1f1;
}
#tvlogin>.qr-container>img {
    width: 21.875vw;
    height: 38.889vh;
    position: absolute;
    left: 0;
    bottom: 7.407vh;
}
#tvlogin>.qr-container>button {
    text-align: center;
    position: absolute;
    left: 8vw;
    bottom: 0;
    font-size: 4vh;
    background: none;
    color: #f1f1f1;
}
#tvlogin>.website {
    font-family: "小米兰亭";
    color: #f1f1f1;
    font-size: 2.963vh;
    position: absolute;
    right: 6.25vw;
    bottom: 6.481vh;
}



