
.MyConfirm {
    width: 20vw;
    height: 25vh;
    position: absolute;
    top: 30vh;
    left: 40vw;
    z-index: 100;
    font-family: "小米兰亭";
    font-size: 2.778vh;
    color: #f1f1f1;
    border: 0.104vw solid #eeeeee;
    color: #f1f1f1;
    overflow: hidden;
}
.MyConfirm>img {
    width: 20vw;
    height: 25vh;
    position: absolute;
    top: 0;
    left: 0;
}
.MyConfirm>.sure {
    width: 100%;
    height: 15vh;
    position: absolute;
    top: 0;
    left: 0;
}
.MyConfirm>.sure>p {
    text-align: center;
    margin: 5vh auto;
    letter-spacing: 0.08vw;
}
.MyConfirm>.button-group {
    width: 100%;
    height: 10vh;
    position: absolute;
    top: 15vh;
    left: 0;
}
.MyConfirm>.button-group>.yes-bt {
    width: 3.5vw;
    height: 4vh;
    position: absolute;
    left: 5vw;
    background: none;
    border: none;
    font-size: 2.778vh;
    color: #f1f1f1;
}
.MyConfirm>.button-group>.no-bt {
    width: 3.5vw;
    height: 4vh;
    position: absolute;
    right: 5vw;
    background: none;
    border: none;
    font-size: 2.778vh;
    color: #f1f1f1;
}




